import React, {
  useState,
  useRef,
  useEffect,
  createContext,
  useContext,
} from "react";
import { useSelector, useDispatch } from "react-redux";
// styles
import styles from "./index.module.scss";
// internal components
import { useForm } from "react-hook-form";
import NormalButton from "components/UI/common/NormalButton/NormalButton";
//import InputGroup from "components/UI/common/InputGruops";
import { sleep } from "Constants/helperFunctions";
import NormalMultiSelect from "components/UI/common/NormalMultiSelect";
import TabComponent from "components/UI/common/Tab/TabComponent";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import ServiceTable from "./ServiceTable";
import IconButton from "@mui/material/IconButton";
import CommonCheckBox from "components/UI/common/CheckBox/CommonCheckBox";
import AddCircleOutlineIcon from "@mui/icons-material/AddCircleOutline";
import {
  getFrequencies,
  getRoomType,
  listServiceAmenity,
  addServicesAmenities,
  deleteServicesAmenities,
} from "service/serviceAmenity";
import { addPropertyCreateRoomType } from "service/StayDetails";
import { setTabDirty, setTabStatus } from "redux/slice/basicSlice";
import ToolTip from "components/UI/common/Tooltip";
import { getTabStatusDetails } from "service/TabDetails";
import { Toast } from "Constants/toast";
import { useCallback } from "react";
import DeleteModal from "components/UI/common/DeleteModal";
import { ReactComponent as InfoIcon } from "assets/images/infoIcon.svg";
import debounce from "lodash/debounce";
import FormErrorMessage from "components/UI/common/ErrorMessage";
import { ErrorOutlineOutlined } from "@mui/icons-material";

const ServiceForm = ({
  handleNextTab,
  showPreview,
  activeTab,
  isPreviewScreen,
  childSaveDraftRef,
}) => {
  const {
    register,
    handleSubmit,
    getValues,
    setValue,
    reset,
    control,
    formState: { errors },
    watch,
  } = useForm({
    mode: "onChange",
    showCommonError: false,
  });

  const formRef = useRef();
  const propertyId = useSelector((state) => state.basic.propertyId);
  const UnitCreationDraft = useSelector(
    (state) => state.basic.unitCreationDraft
  );
  const PropertyStatus = useSelector((state) => state.basic.propertyStatus);
  const TotalDraft = useSelector((state) => state.basic.TotalDraft);
  const PendingProperty = useSelector(
    (state) => state.basic.showPendingProperty
  );

  const dispatch = useDispatch();
  const [activeSubTab, setActiveSubTab] = useState(0);
  const [tabValue, setTabValue] = useState("services");
  const [commonSelectAll, setCommonSelectAll] = useState({
    services: {},
    amenities: {},
  });
  const [selectedFrequency, setSelectedFrequency] = useState("ALL");
  const [frequencyList, setFrequencyList] = useState([]);
  const [roomTypes, setRoomTypes] = useState([]);
  const [activeRoomType, setActiveRoomType] = useState("");
  const [data, setData] = useState([]);
  const [isEdit, setIsEdit] = useState(false);
  const [serviceData, setServiceData] = useState([]);
  const [amenityData, setAmenityData] = useState([]);
  const [triggerAppend, setTriggerAppend] = useState(false);
  const [checkboxdata, setcheckboxdata] = useState({});
  const [isDisabled, setIsDisabled] = useState(false);
  const [saveDraft, setSaveDraft] = useState(false);
  const [deletedArray, setDeletedArray] = useState([]);
  const [tabStatus, setTabStatusValue] = useState("up next");
  const [showWarning, setShowWarning] = useState(null);
  const [prevSelectedFrequency, setPrevSelectedFrequency] = useState("");
  const [isDirty, setIsDirty] = useState(false);
  const [showCommonError, setShowCommonErrorMessage] = useState(false);

  useEffect(() => {
    dispatch(setTabDirty(isDirty));
  }, [isDirty]);

  const getTabData = useCallback(async () => {
    try {
      const response = await getTabStatusDetails(propertyId);
      if (response.status === 200) {
        const filteredData = response.data?.find(
          (o) => o?.tabName?.toLowerCase() === "service amenities"
        );
        setTabStatusValue(filteredData?.tabStatus?.toLowerCase());
      } else {
        Toast({ type: "error", message: response.data.message });
      }
    } catch (e) {
      console.log("e :>> ", e);
    }
  }, []);

  useEffect(() => {
    setTimeout(() => {
      getTabData();
    }, 500);
  }, [getTabData]);

  const getFrequencyList = () => {
    getFrequencies().then((response) => {
      if (response?.data?.data) {
        const frequencies = response.data.data;
        let tranformData = [
          ...frequencies.map((frequency) => ({
            value: frequency.name,
            label: frequency.name,
            ...frequency,
          })),
        ];

        setFrequencyList([...tranformData]);
      }
    });
  };

  const getRoomTypeList = () => {
    getRoomType(propertyId).then((response, i) => {
      if (response?.data?.data?.length) {
        const newCommonSelectAll = { ...commonSelectAll };
        const transformData = response?.data?.data?.map((roomType) => ({
          ...roomType,
          index: i + 1,
          title: roomType.roomType,
        }));
        const newServicedata = response.data.data.map((roomType) => {
          const showRetailShortStay = roomType?.roomCategories.some(
            ({ category, shortStay }) => category === "Retail" && shortStay
          );
          const showRetailLongStay = roomType?.roomCategories.some(
            ({ category, longStay }) => category === "Retail" && longStay
          );
          const showCorporateLongStay = roomType?.roomCategories.some(
            ({ category, longStay }) => category === "Corporate" && longStay
          );
          const showCorporateShortStay = roomType?.roomCategories.some(
            ({ category, shortStay }) => category === "Corporate" && shortStay
          );

          newCommonSelectAll.services[roomType?.roomType.toLowerCase()] = false;
          return {
            bsgp: roomType?.roomType.toLowerCase(),
            propertyId,
            roomTypeId: roomType?._id,
            retailShortStay: showRetailShortStay,
            retailLongStay: showRetailLongStay,
            corporateLongStay: showCorporateLongStay,
            corporateShortStay: showCorporateShortStay,
            roomType: roomType?.roomType,
            data: [
              {
                frequency: "",
                chargingType: "",
                rate: "",
                ...(showRetailShortStay ? { retailShortStay: false } : {}),
                ...(showRetailLongStay ? { retailLongStay: false } : {}),
                ...(showCorporateShortStay
                  ? { corporateShortStay: false }
                  : {}),
                ...(showCorporateLongStay
                  ? { showCorporateLongStay: false }
                  : {}),
                propertyId,
                roomTypeId: roomType?._id,
                itemType: "SERVICE",
                roomType: roomType?.roomType,
                name: "",
                frequencyId: "",
              },
            ],
            type: "services",
          };
        });
        const newAmenitydata = response?.data?.data?.map((roomType) => {
          const showRetailShortStay = roomType?.roomCategories.some(
            ({ category, shortStay }) => category === "Retail" && shortStay
          );
          const showRetailLongStay = roomType?.roomCategories.some(
            ({ category, longStay }) => category === "Retail" && longStay
          );
          const showCorporateLongStay = roomType?.roomCategories.some(
            ({ category, longStay }) => category === "Corporate" && longStay
          );
          const showCorporateShortStay = roomType?.roomCategories.some(
            ({ category, shortStay }) => category === "Corporate" && shortStay
          );
          newCommonSelectAll.amenities[
            roomType?.roomType.toLowerCase()
          ] = false;
          return {
            bsgp: roomType?.roomType?.toLowerCase(),
            propertyId,
            roomTypeId: roomType?._id,
            retailShortStay: showRetailShortStay,
            retailLongStay: showRetailLongStay,
            corporateLongStay: showCorporateLongStay,
            corporateShortStay: showCorporateShortStay,
            roomType: roomType?.roomType,
            data: [
              {
                frequency: "",
                chargingType: "",
                rate: "",
                ...(showRetailShortStay ? { retailShortStay: false } : {}),
                ...(showRetailLongStay ? { retailLongStay: false } : {}),
                ...(showCorporateShortStay
                  ? { corporateShortStay: false }
                  : {}),
                ...(showCorporateLongStay
                  ? { showCorporateLongStay: false }
                  : {}),
                propertyId,
                roomTypeId: roomType?._id,
                itemType: "AMENITY",
                roomType: roomType?.roomType,
                name: "",
                frequencyId: "",
              },
            ],
            type: "amenities",
          };
        });
        let tempObj = {};
        const mergeData = [...newServicedata, ...newAmenitydata];
        mergeData.forEach((o, i) => {
          if (!tempObj?.[o.type]) {
            tempObj[o.type] = {
              [o.roomType.toLowerCase()]: {
                retailLongStay: false,
                retailShortStay: false,
                corporateLongStay: false,
                corporateShortStay: false,
              },
            };
          } else {
            tempObj[o.type][o.roomType.toLowerCase()] = {
              retailLongStay: false,
              retailShortStay: false,
              corporateLongStay: false,
              corporateShortStay: false,
            };
          }
        });

        setCommonSelectAll(newCommonSelectAll);
        setcheckboxdata(tempObj);
        setData(mergeData);
        setRoomTypes([...transformData]);
        setActiveRoomType(transformData[0].title.toLowerCase());
      }
    });
  };

  useEffect(() => {
    getFrequencyList();
    getRoomTypeList();
  }, []);

  useEffect(() => {
    if (amenityData.length && serviceData.length) {
      const mergeData = [...(serviceData || []), ...(amenityData || [])].filter(
        Boolean
      );
      formRef?.current?.methods?.reset((newValue) => {
        return {
          form: [...mergeData],
        };
      });
    }
  }, [amenityData, serviceData]);

  useEffect(() => {
    if (propertyId && selectedFrequency && data.length) {
      fetchServiceAmenityList("SERVICE");
      fetchServiceAmenityList("AMENITY");
    }
  }, [selectedFrequency]);

  useEffect(() => {
    if (propertyId && selectedFrequency && data.length) {
      fetchServiceAmenityList("SERVICE");
      fetchServiceAmenityList("AMENITY");
    }
  }, [propertyId, data]);

  const fetchServiceAmenityList = (status) => {
    const params = {
      propertyId,
      frequency: selectedFrequency || "",
      itemType: status,
    };
    listServiceAmenity(params).then((response) => {
      let form = data;
      if (response?.data?.data?.length) {
        const { data: respData } = response.data;
        let transformData = respData.reduce((acc, item) => {
          const { roomType, roomTypeId, propertyId, itemType } = item;
          const {
            retailShortStay,
            retailLongStay,
            corporateLongStay,
            corporateShortStay,
            ...rest
          } = item;
          const typeitem = itemType === "SERVICE" ? "services" : "amenities";

          // Check already existed roomTypeId in BE data
          const index = acc.findIndex(
            ({ roomTypeId, type }) =>
              roomTypeId === item.roomTypeId && type === typeitem
          );
          const roomTypeData = form.find(
            ({ roomTypeId, type }) =>
              roomTypeId === item.roomTypeId && type === typeitem
          );

          if (index === -1) {
            let tempObj = {
              ...rest,
              ...(roomTypeData?.retailShortStay ? { retailShortStay } : {}),
              ...(roomTypeData?.retailLongStay ? { retailLongStay } : {}),
              ...(roomTypeData?.corporateLongStay ? { corporateLongStay } : {}),
              ...(roomTypeData?.corporateShortStay
                ? { corporateShortStay }
                : {}),
            };
            acc = [
              {
                bsgp: roomType.toLowerCase(),
                roomTypeId,
                propertyId,
                type: typeitem,
                data: [tempObj],
                retailShortStay: roomTypeData?.retailShortStay,
                retailLongStay: roomTypeData?.retailLongStay,
                corporateLongStay: roomTypeData?.corporateLongStay,
                corporateShortStay: roomTypeData?.corporateShortStay,
                roomType: roomType,
              },
              ...acc,
            ];
          } else {
            acc[index].data.push({
              ...rest,
              ...(roomTypeData?.retailShortStay ? { retailShortStay } : {}),
              ...(roomTypeData?.retailLongStay ? { retailLongStay } : {}),
              ...(roomTypeData?.corporateLongStay ? { corporateLongStay } : {}),
              ...(roomTypeData?.corporateShortStay
                ? { corporateShortStay }
                : {}),
            });
          }
          return acc;
        }, []);

        transformData = transformData.map((item, i) => {
          const seenNames = new Set();
          item.data = item.data.map((item2) => {
            const name = item2.name;
            if (seenNames.has(name)) {
              item2.name = null;
            } else {
              seenNames.add(name);
            }
            return item2;
          });
          return item;
        });
        if (transformData?.length !== roomTypes?.length) {
          roomTypes.map((roomType) => {
            const isAvailable = transformData?.findIndex(
              (a) => a.roomType === roomType?.roomType
            );
            if (isAvailable === -1) {
              transformData.push({
                bsgp: roomType?.roomType?.toLowerCase?.(),
                propertyId,
                roomType: roomType?.roomType,
                roomTypeId: roomType?._id,
                type: status === "SERVICE" ? "services" : "amenities",
                data: [],
                retailShortStay: false,
                retailLongStay: false,
                corporateLongStay: false,
                corporateShortStay: false,
              });
            }
          });
        }
        setIsEdit(true);
        if (status === "SERVICE") setServiceData(transformData);
        if (status === "AMENITY") setAmenityData(transformData);

        formRef?.current?.prefillCheckbox(transformData);
        formRef?.current?.prefillSelectAll(transformData);
      } else {
        let updatedData = roomTypes?.map((roomType) => ({
          bsgp: roomType?.roomType?.toLowerCase?.(),
          propertyId,
          roomType: roomType?.roomType,
          roomTypeId: roomType?._id,
          type: status === "SERVICE" ? "services" : "amenities",
          data: [],
          retailShortStay: false,
          retailLongStay: false,
          corporateLongStay: false,
          corporateShortStay: false,
        }));
        if (status === "SERVICE") setServiceData(updatedData);
        if (status === "AMENITY") setAmenityData(updatedData);
        formRef?.current?.prefillCheckbox(updatedData);
        formRef?.current?.prefillSelectAll(updatedData);
      }
    });
  };

  const handleSaveDraft = (e, isValidate) => {
    setIsDisabled(true);
    setSaveDraft(true);
    const form = formRef?.current?.methods?.watch();
    onsubmit(form, isValidate);
  };

  const onsubmit = async (data, isValidate) => {
    const serviceAmenities = data.form.reduce((acc, field) => {
      let name = null;
      field.data.forEach((o) => {
        const { __v, createdAt, updatedAt, isDraft, _id, ...rest } = o;
        if (rest.name == null) {
          rest.name = name;
        } else {
          name = rest.name;
        }
        acc.push({
          ...rest,
          ...(_id ? { id: _id } : {}),
        });
      });
      return acc;
    }, []);
    const form = {
      propertyId,
      isDraft: isValidate === true,
      ...(isEdit ? { deletedArray } : {}),
      serviceAmenities,
    };
    //await sleep(1000);
    try {
      await addServicesAmenities(form, isEdit);
      dispatch(setTabDirty(false));
      dispatch(setTabStatus());
      setIsDisabled(false);
      if (isValidate !== true) {
        if (isPreviewScreen)
          handleNextTab(activeTab, false, isPreviewScreen, true);
        else handleNextTab(null, false, false, true);
      }
      setTimeout(() => {
        setSaveDraft(false);
        getTabData();
      }, 500);
    } catch (e) {
      setIsDisabled(false);
      // handle your error
    }
  };
  console.log(activeSubTab, "activsub");
  console.log(tabValue, "tabValue");
  console.log(activeRoomType, "RoomtabValue");
  const handleOnSubmit = async (e) => {
    await setShowCommonErrorMessage(false);
    setIsDisabled(true);
    formRef.current?.methods?.handleSubmit(onsubmit, (err) => {
      console.log(formRef.current?.methods?.watch("form"), "formref");
      console.log(err, "clerrror");
      const index = err.form.findIndex((o) => o?.hasOwnProperty("data"));
      console.log(index);
      setIsDisabled(false);
      setShowCommonErrorMessage(true);
      if (index !== -1 && index !== undefined) {
        let finalAffectedData = formRef.current?.methods?.watch("form") || [];
        console.log(finalAffectedData, "finalaffectDt");
        if (finalAffectedData && finalAffectedData.length > 0) {
          const MergedErrorArr = err.form
            .map((o, i) => {
              if (o?.hasOwnProperty("data")) {
                return `${finalAffectedData[i].type} # ${finalAffectedData[i].bsgp}`;
              }
            })
            .filter(Boolean);
          console.log(MergedErrorArr, "mergearray");
          if (MergedErrorArr && MergedErrorArr.length > 0) {
            if (!MergedErrorArr.includes(`${tabValue} # ${activeRoomType}`)) {
              setTabValue(MergedErrorArr[0]?.split(" # ")[0]);
              setActiveRoomType(MergedErrorArr[0]?.split(" # ")[1]);
              setIsDisabled(false);
              const activesubIndex = roomTypes.findIndex(
                (o) =>
                  o.title.toLowerCase() === MergedErrorArr[0].split(" # ")[1]
              );
              setActiveSubTab(activesubIndex);
              setShowCommonErrorMessage(true);
            }
          }
          // for (let i in err.form) {
          //   debugger
          //   if (err.form[i]?.hasOwnProperty("data")) {
          //     if (tabValue === finalAffectedData[i].type && activeRoomType === finalAffectedData[i].bsgp) {
          //       setIsDisabled(false);
          //       break;
          //     } else {
          //       setTabValue(finalAffectedData[i].type);
          //       setActiveRoomType(finalAffectedData[i].bsgp);
          //       setIsDisabled(false);
          //       const activesubIndex = roomTypes.findIndex(
          //         (o) => o.title.toLowerCase() === finalAffectedData[i].bsgp
          //       );

          //       setActiveSubTab(activesubIndex);
          //       setShowCommonErrorMessage(true);
          //       break;
          //     }
          //   } else {
          //     setIsDisabled(false);
          //   }
          // }
        }
      }
    })(e);
  };

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };

  const onSelect = async (e, newValue) => {
    if (isDirty == false) setSelectedFrequency(e.target.value);
    else setShowWarning(e.target.value);
  };

  const handleDeleteArray = (_id) => {
    if (_id) {
      setDeletedArray((prev) => [...prev, { _id }]);
    }
  };

  return (
    <div className={`${styles.serviceForm}`}>
      <div className={`${styles.contentPart} px-3`}>
        <div className="new-section py-2">
          <div className="row mb-3 align-items-center">
            <div className="col-6">
              <Tabs
                value={tabValue}
                onChange={handleTabChange}
                aria-label="wrapped label tabs example"
                classes={{ indicator: styles.tabIndicator }}
              >
                {["Services", "Amenities"].map((tab, i) => (
                  <Tab
                    key={i}
                    value={tab.toLowerCase()}
                    label={tab}
                    classes={{
                      root: styles.tabRoot,
                      selected: styles.selectedTab,
                    }}
                  />
                ))}
              </Tabs>
            </div>
            <div className="col-6">
              <div className={styles.selectwrap}>
                <ToolTip label="Filters are only applicable for saved data.">
                  <InfoIcon height={30} width={30} />
                </ToolTip>
                <NormalMultiSelect
                  placeholder={"Frequency: All"}
                  options={[{ value: "ALL", label: "All" }, ...frequencyList]}
                  handleChange={onSelect}
                  value={selectedFrequency}
                  disabled={
                    String(tabStatus).toUpperCase() === "UP NEXT" ||
                    isDirty === true
                      ? true
                      : false
                  }
                  supportLabel={"Frequency: "}
                />
              </div>
            </div>
          </div>
        </div>

        <div className={styles.tabwrap}>
          <div className="row">
            <div className="col-6">
              <div className={styles.tabWrapper}>
                <TabComponent
                  tabs={roomTypes}
                  setActiveTab={(i) => setActiveSubTab(i)}
                  isRoomPackage
                  activeTab={activeSubTab}
                  setActiveTabName={(val) =>
                    setActiveRoomType(val.toLowerCase())
                  }
                />
              </div>
            </div>
            <div className="col-6">
              {!showPreview && (
                <div className="d-flex align-items-center justify-content-end">
                  <div className={styles.checkboxWrap}>
                    <CommonCheckBox
                      customClass={styles.checkbox}
                      onChange={() => {
                        formRef.current.setChecked(
                          !commonSelectAll?.[tabValue]?.[activeRoomType]
                        );
                        setCommonSelectAll((prev) => ({
                          ...prev,
                          [tabValue]: {
                            ...prev[tabValue],
                            [activeRoomType]: !prev[tabValue][activeRoomType],
                          },
                        }));
                      }}
                      isChecked={commonSelectAll?.[tabValue]?.[activeRoomType]}
                    />
                    <span className={styles.selectText}>Select All</span>
                  </div>
                  <div className={styles.hr} />
                  <a
                    id={"addNewService"}
                    className={`${styles.addServiceWrap} ${
                      selectedFrequency !== "ALL"
                        ? styles["cursor-disable"]
                        : ""
                    }`}
                    selectedFrequency={selectedFrequency}
                  >
                    <IconButton
                      className={`${styles.iconbtn}  ${
                        selectedFrequency !== "ALL"
                          ? styles["cursor-disable"]
                          : ""
                      }`}
                      onClick={
                        () => {}
                        //handleAdd(i, key)
                      }
                    >
                      <AddCircleOutlineIcon className={styles.circleAdd} />
                    </IconButton>

                    {tabValue == "services" ? (
                      <span className={styles.addServiceText}>
                        Add New Service
                      </span>
                    ) : (
                      <span className={styles.addServiceText}>
                        Add New Amenity
                      </span>
                    )}
                  </a>
                </div>
              )}
            </div>
          </div>
        </div>
        <div>
          {data.length > 0 && Object.keys(checkboxdata).length > 0 && (
            <ServiceTable
              tabValue={tabValue}
              ref={formRef}
              activeTabLabel={activeRoomType}
              commonSelectAll={commonSelectAll}
              setCommonSelectAll={setCommonSelectAll}
              data={data}
              frequencyList={frequencyList}
              triggerAppend={triggerAppend}
              setTriggerAppend={setTriggerAppend}
              getRoomTypeList={getRoomTypeList}
              checkboxdata={checkboxdata}
              handleDeleteArray={handleDeleteArray}
              setFrequencyList={setFrequencyList}
              saveDraft={saveDraft}
              selectedFrequency={selectedFrequency}
              filterUpdate={() => {
                if (propertyId && selectedFrequency && data.length) {
                  fetchServiceAmenityList("SERVICE");
                  fetchServiceAmenityList("AMENITY");
                }
              }}
              setShowWarning={() => setShowWarning(null)}
              setIsDirty={setIsDirty}
              showPreview={showPreview}
            />
          )}
        </div>
      </div>

      {!TotalDraft && PropertyStatus == "PENDING" ? <></> : <hr />}
      {/* <div className={`d-flex justify-content-end mx-3`}>
        {showCommonError &&
          <span
            className={`text-danger d-flex align-items-center fs-12 fw-500`}
          >
            <ErrorOutlineOutlined fontSize="12px" /> Field data required to be filled
          </span>
        }
      </div> */}
      {showPreview ? (
        !PendingProperty ? (
          <>
            {!TotalDraft && PropertyStatus !== "PENDING" && (
              <div className="d-flex justify-content-start">
                <div
                  className={`d-flex justify-content-start ${styles.btn} py-2 px-2 col`}
                >
                  <div className={styles.btnWidth}>
                    {PropertyStatus == "APPROVED" &&
                      UnitCreationDraft == false && (
                        <NormalButton
                          className={"draftBtn"}
                          label={"Add More Units"}
                          //id={"draftBtn"}
                          //onClick={(e) => handleSaveDraft(e)}
                          onClick={(e) => {}}
                        />
                      )}
                  </div>
                </div>
                <div
                  className={`d-flex justify-content-end ${styles.btn} py-2 px-2 col`}
                >
                  <div className={styles.btnWidth}>
                    {PropertyStatus !== "REJECTED" ? (
                      <NormalButton
                        className={"draftBtn"}
                        label={"Edit Details"}
                        //id={"draftBtn"}
                        //onClick={(e) => handleSaveDraft(e)}
                        onClick={(e) => {
                          handleNextTab(activeTab, true);
                        }}
                      />
                    ) : (
                      <NormalButton
                        className={"submitBtn"}
                        label={"Edit Details"}
                        //id={"draftBtn"}
                        //onClick={(e) => handleSaveDraft(e)}
                        onClick={(e) => {
                          handleNextTab(activeTab, true);
                        }}
                      />
                    )}
                  </div>
                  {(UnitCreationDraft == "" || UnitCreationDraft == null) &&
                  PropertyStatus !== "REJECTED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Add Units"}
                        id="AddunitBtn"
                        onClick={debounce(async (e) => {
                          //handleNextTab();
                        }, 800)}
                      />
                    </div>
                  ) : UnitCreationDraft == true &&
                    PropertyStatus !== "REJECTED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Continue Unit Creation"}
                        id="AddunitBtn"
                        onClick={debounce(async (e) => {
                          //handleNextTab();
                        }, 800)}
                      />
                    </div>
                  ) : UnitCreationDraft == false &&
                    PropertyStatus !== "REJECTED" ? (
                    <div className={styles.btnWidth}>
                      <NormalButton
                        className={"submitBtn"}
                        label={"Add More Units"}
                        id="AddMoreBtn"
                        onClick={debounce(async (e) => {
                          //handleNextTab();
                        }, 800)}
                      />
                    </div>
                  ) : UnitCreationDraft == false &&
                    PropertyStatus == "APPROVED" ? (
                    <div className={styles.btnWidth}>c</div>
                  ) : (
                    <></>
                  )}
                </div>
              </div>
            )}{" "}
          </>
        ) : (
          <>
            <div className="d-flex justify-content-start">
              <div
                className={`d-flex justify-content-end ${styles.btn} py-2 px-2 col`}
              >
                <div className={styles.btnWidth}>
                  <NormalButton
                    className={"draftBtn"}
                    label={"Edit Details"}
                    //id={"draftBtn"}
                    //onClick={(e) => handleSaveDraft(e)}
                    onClick={(e) => {
                      handleNextTab(activeTab, true);
                    }}
                  />
                </div>
                <div className={styles.btnWidth}>
                  <NormalButton
                    className={"submitBtn"}
                    label={"Submit"}
                    id="submitBtn"
                    onClick={debounce(async (e) => {
                      handleNextTab(null, false, false, true);
                    }, 800)}
                  />
                </div>
              </div>
            </div>
          </>
        )
      ) : (
        <div className={`d-flex justify-content-end ${styles.btn} py-2 px-2`}>
          <div className="d-flex align-items-center">
            <div className="px-1">
              {selectedFrequency !== "ALL" ? (
                <ToolTip label="Remove all the filters to enable actions">
                  <InfoIcon className="mt-2" />
                </ToolTip>
              ) : (
                ""
              )}
            </div>
          </div>
          <div
            className={`${styles.btnwidth}  ${
              selectedFrequency !== "ALL" ? styles["cursor-disable"] : ""
            }`}
          >
            <NormalButton
              className={`draftBtn ${
                selectedFrequency !== "ALL" ? styles["cursor-disable"] : ""
              }`}
              label={isPreviewScreen ? "Cancel" : "Save as Draft"}
              //id={"draftBtn"}
              innerref={childSaveDraftRef}
              onClick={debounce((e) => {
                if (isPreviewScreen) {
                  handleNextTab(activeTab, false, isPreviewScreen);
                  return;
                }
                setIsDisabled(true);
                setTimeout(() => {
                  handleSaveDraft(e, true);
                }, 500);
              }, 800)}
              // onClick={async (e) => {
              //   setIsDisabled(true);
              //   await setTimeout(() => {
              //     handleSaveDraft(e, true);
              //   }, 500);
              // }}
              disabled={selectedFrequency !== "ALL" || isDisabled}
            />
          </div>
          <div
            className={`${styles.btnwidth}  ${
              selectedFrequency !== "ALL" ? styles["cursor-disable"] : ""
            }`}
          >
            <NormalButton
              className={`submitBtn  ${
                selectedFrequency !== "ALL" ? styles["cursor-disable"] : ""
              }`}
              label={isPreviewScreen ? "Save Changes" : "Save & Proceed"}
              id="submitBtn"
              //onClick={handleNextTab}
              onClick={debounce(handleOnSubmit, 800)}
              //onClick={handleOnSubmit}
              disabled={selectedFrequency !== "ALL" || isDisabled}
            />
          </div>
        </div>
      )}

      <DeleteModal
        visible={showWarning ? true : false}
        handleClose={() => {
          setShowWarning(null);
          //setSelectedFrequency(showWarning);
        }}
        handleDelete={() => {
          setShowWarning(null);
          setSelectedFrequency(showWarning);
        }}
        description={`Your unsaved changes would be lost, is that okay?`}
        label="Continue"
      />
    </div>
  );
};

export default ServiceForm;
